/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

h1.logo {
  text-align: center;
  margin-bottom: 46px;
  .header__logo {
    height: 24px;
    display: inline-block;
    margin: 0 auto;
    img {
      display: block;
      width: auto;
      max-height: 24px; } } }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 18px 36px;
  margin-left: 205px;
  background: #F6F6F7;
  box-shadow: 4px 4px 8px #6C6C6C0F;
  z-index: 99;
  &-container {
    max-width: calc(100% - 205px); }

  .row {
    align-items: center; }
  .col.right {
    display: flex;
    justify-content: flex-end; }

  .write-brief {
    .btn-brief {
      padding: 9px 15px;
      padding-left: 55px;
      position: relative;
      color: $primary;
      font-size: 16px;
      line-height: 24px;
      background: #F5D9D4;
      border: 0.2px solid var(--unnamed-color-f23c13);
      border-radius: 8px;
      font-weight: 500;
      text-decoration: none;
      display: inline-block;
      &:before {
        content: "";
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 8px;
        background: $primary;
        background-image: url('../images/ic-plus-btn.png');
        background-position: center;
        background-size: 14px;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0; } } }

  .user-header {
    &>ul {
      margin-left: -15px;
      margin-right: -15px;
      &>li {
        padding: 0 10px;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        & > a {
          display: inline-block;
          vertical-align: middle;
          width: 28px;
          height: 28px;
          border-radius: 50%;
          & > img {
            display: block;
            max-width: 100%;
            height: 100%; } } } }
    .notif {
      position: relative;
      & > a {
        transition: all .3s ease; }
      &:hover {
        & > a {
          transform: rotate(10deg); } } }
    .user-acnt {
      & > a {
        border: 2px solid #EDEDED; }
      &:hover {
        & > a {
          border-color: #000; } } }
    .head-search {
      position: relative;
      .box_search {
        position: absolute;
        transition: all .4s ease;
        width: 400px;
        top: -8px;
        right: 0;
        max-width: 0;
        opacity: 0;
        visibility: hidden;
        .form-group {
          margin-bottom: 0; }
        .form-control {
          height: 42px;
          border-color: #FFF; }
        .btn-submit {
          position: absolute;
          top: 0;
          right: 0;
          width: 42px;
          height: 42px;
          background: $primary;
          padding: 0;
          border-radius: 5px;
          img {
            width: 20px;
            margin: 0 auto; } } }

      &.hs-show {
        .box_search {
          max-width: 400px;
          opacity: 1;
          visibility: visible; } } } }

  &.no-sidebar {
    margin-left: 0;
    .header-container {
      max-width: 100%; } }

  h1.logo {
    text-align: left;
    margin-bottom: 0;
    line-height: 0em; } }

.user-account {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 34px;
  margin: 0 auto;
  text-align: center;
  &__pp {
    margin-bottom: 8px;
    display: inline-block; }
  &__name {
    font-size: 14px;
    line-height: 1.5em;
    color: $text;
    font-weight: 500;
    span {
      display: inline-block;
      vertical-align: middle; }
    &:after {
      content: "";
      display: inline-block;
      vertical-align: middle;
      margin-left: 8px;
      background-image: url('../images/ic-caret-right.svg');
      @include bgcontain;
      width: 16px;
      height: 16px;
      position: relative;
      transform: rotate(90deg); } }
  &.dd-show {
    .dropdown-menu.user-acnt_menu {
      display: block;
      top: auto;
      bottom: 0; } } }
.dropdown-menu.user-acnt_menu {
  padding: 12px;
  margin-left: 0;
  border-radius: 8px;
  &.show {
    top: 50px !important;
    left: 170px !important; } }
.user-acnt_menu {
  // border-radius: 8px;
  // background: #fff;
  // box-shadow: 4px 4px 8px #6C6C6C0F;
  // border: 1px solid #EDEDED
  // position: absolute;
  // min-width: 326px;
  // padding: 12px
  // top: 50px;
  // right: 12px;
  // opacity: 0;
  // visibility: hidden;
  // transition: all .5s ease;
  // overflow: hidden;
  ul {
    li {
      margin-bottom: 8px;
      min-width: 300px;
      &:last-child {
        margin-bottom: 0; }
      a {
        display: block;
        padding: 10px;
        color: $text;
        border-radius: 8px;
        text-decoration: none; }
      .ic {
        display: inline-block;
        vertical-align: middle;
        width: 15px;
        height: 15px;
        margin-right: 10px;
        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: contain; } }
      &:hover {
        a {
          background: #F4D9D3; } } } } }

.usracnt-open {
  .header .user-header {
    .user-acnt_menu {
      top: 40px;
      opacity: 1;
      visibility: visible; } } }



// Sidebar Menu
.sidebar {
  padding: 24px 12px;
  background: #fff;
  display: block;
  width: 205px;
  height: 100vh;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  background: #fff;
  h1.logo {
    text-align: center;
    margin-bottom: 46px;
    .header__logo {
      height: 24px;
      display: inline-block;
      margin: 0 auto;
      img {
        display: block;
        width: auto;
        max-height: 24px; } } } }

.sidemenu {
  ul {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #EDEDED;
    li {
      margin-bottom: 4px;
      a {
        display: block;
        padding: 12px 20px;
        border-radius: 8px;
        color: $text;
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        text-decoration: none;
        .icon {
          width: 20px;
          height: 20px;
          margin-right: 14px;
          display: inline-block;
          vertical-align: middle;
          img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            &.hover {
              display: none; } } }
        .count {
          font-size: 14px;
          line-height: 20px;
          padding: 0 6px;
          background: $primary;
          color: #fff;
          border-radius: 4px;
          display: inline-block;
          margin-left: 8px; } }
      &:hover,
      &.active,
      &.current-menu-item {
        a {
          background: #F4D9D3;
          color: $primary;
          .icon {
            img:not(.hover) {
              display: none; }
            img.hover {
              display: block; } } } } } } }

.dontknow-box {
  padding: 24px 15px;
  padding-left: 20px;
  background: #F6F6F6;
  border-radius: 8px;
  .icon {
    width: 34px;
    height: 34px;
    border-radius: 5px;
    margin-bottom: 14px;
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain; } }
  h4 {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: $primary;
    margin-bottom: 18px; }
  a {
    font-size: 12px;
    line-height: 18px;
    color: $text; } }

.need-help {
  text-align: center;
  a {
    color: $text;
    font-size: 12px;
    line-height: 36px;
    text-decoration: none; } }

.needhelp {
  position: absolute;
  width: 100%;
  left: 12px;
  bottom: 24px;
  text-align: center;
  .img {
    width: 80px;
    height: 53px;
    margin: 0 auto 4px;
    img {
      display: block;
      width: 100%;
      height: 100%; } }
  a {
    color: $text; } }


// Notification
.header-notif__box {
  border-radius: 8px;
  background: #fff;
  box-shadow: 4px 4px 8px #6C6C6C0F;
  border: 1px solid #A8A8A8;
  position: absolute;
  min-width: 385px;
  top: 70px;
  right: 0px;
  opacity: 0;
  visibility: hidden;
  transition: all .5s ease;
  overflow: hidden;
  // z-index: 112
  @include mobile {
    right: 0 !important;
    left: 0;
    margin: 0 auto; }
  h3 {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 16px;
    font-weight: bold; }
  .n-wrapper {
    padding: 16px 20px;
    max-height: 520px;
    overflow: hidden;
    overflow-y: auto;
    // max-width: calc(100% - 1px)
    border-radius: 6px;
    &::-webkit-scrollbar {
      width: 3px;
      background-color: #F9F9F9;
      border-radius: 3px; }
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 3px rgba(0,0,0,0.2);
      background-color: #F5F5F5; }
    &::-webkit-scrollbar-thumb {
      background-color: $primary;
      // border: 1px solid #F9F9F9
      border: 0;
      border-radius: 3px; } }
  .pertime {
    font-size: 12px;
    line-height: 16px;
    color: #000000;
    margin-bottom: 20px;
    .t-notif {
      color: #000000; } }
  .notif-item {
    padding: 10px;
    border-radius: 10px;
    border-bottom: 1px solid $border;
    background: #fff;
    display: flex;
    margin-bottom: 10px;
    position: relative;
    &:not(:last-child) {
      margin-bottom: 10px; }
    .icon {
      width: 30px !important;
      height: 30px !important;
      margin-right: 16px;
      background: $primary;
      border-radius: 50%;
      flex: 0 0 30px;
      max-width: 30px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      .ic {
        width: 100%;
        height: 100%;
        max-width: 20px;
        max-height: 20px;
        display: block;
        @include bgcontain; } }
    .box-text {
      margin-top: -5px;
      flex: 0 0 calc(100% - 60px);
      max-width: calc(100% - 60px);
      small {
        font-size: 10px;
        line-height: 14px;
        color: #4C9F70; }
      h4,
      p {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 0;
        max-width: 100%;
        // overflow: hidden
        // text-overflow: ellipsis
 }        // white-space: nowrap
      .nopo {
        margin-bottom: 12px;
        display: block;
        font-size: 10px;
        line-height: 14px;
        max-width: 200px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; }
      .btn-do {
        padding: 7px 22px;
        border-radius: 3px;
        background: #DF5055;
        font-size: 12px;
        line-height: 14px;
        color: #fff !important;
        display: inline-block;
        margin-bottom: 15px; }
      .time {
        font-size: 12px;
        line-height: 16px;
        color: #A8A8A8; } }
    .tag {
      font-size: 8px;
      line-height: 12px;
      padding: 1px 7px;
      border-radius: 3px;
      color: #fff;
      position: absolute;
      top: 9px;
      right: 9px;
      &.new {
        background: #DF5055; } }
    &.new {
      .icon {
        &:after {
          content: "";
          width: 10px;
          height: 10px;
          border-radius: 50%;
          border: 1px solid #FFFFFF;
          background: #00E97F;
          position: absolute;
          right: 0;
          bottom: 0; } } } }


  .clue {
    font-size: 10px;
    line-height: 14px;
    color: #000000;
    text-align: center;
    margin-bottom: 16px; } }

body {
  &.notif-open {
    .header-notif__box {
      opacity: 1;
      visibility: visible;
      top: 60px;
      @include mobile {
        top: 130px; } }
    .header.sticky {
      .header-notif__box {
        top: 60px;
        @include mobile {
          top: 110px; } } } }
