.btn-medium {
  padding: 10px 24px;
  font-size: 14px;
  line-height: 1.3em;
  font-weight: 400; }
.btn-small {
  padding: 5px 28px;
  font-size: 12px;
  line-height: 1.3em;
  font-weight: 400; }

.btn-link-black {
  color: $text;
  text-decoration: underline;
  transition: all .3s ease;
  &:hover {
    color: $secondary; } }

.btn-upload {
  border: 1px solid #ced4da;
  font-size: 16px;
  line-height: 1.7em;
  font-weight: 400;
  color: $primary !important;
  margin-top: 28px;
  min-width: 255px;
  position: relative;
  text-align: left;
  &:after {
    content: "";
    width: 18px;
    height: 18px;
    background-image: url('../images/ic-upload-file.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 12px;
    right: 20px; } }

.link-primary {
  color: $primary;
  transition: all .3s ease;
  &:hover {
    color: $primary;
    text-decoration: underline; } }

.accordion {
  > .card {
    position: relative;
    border: 0;
    border-radius: 30px !important;
    margin-bottom: 30px;
    overflow: hidden;
    background: #F6F6F6;
    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 80%;
      left: 0;
      border-radius: 30px;
      background: #F6F6F6;
      opacity: 0;
      transition: all .3s ease-in-out; }
    &:last-child {
      margin-bottom: 0; }
    &.open:before {
      top: 0;
      opacity: 1;
      transition-delay: .1s; }
    > .card-header {
      position: relative;
      z-index: 2;
      color: #BFBFBF;
      cursor: pointer;
      border-radius: 30px;
      border: 0;
      background: transparent;
      padding: 32px 90px 32px 32px;
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 500;
      transition: all .3s ease .2s;
      margin: 0;
      .chevron {
        position: absolute;
        right: 32px;
        display: block;
        width: 24px;
        top: calc(50% - 12px);
        transform: none;
        transition: all .3s ease;
        .svg {
          display: block;
          width: 24px;
          height: 24px;
          path {
            fill: #BFBFBF !important; } } }
      &:not(.collapsed) {
        border-radius: 30px 30px 0 0;
        color: $text;
        .chevron {
          transform: rotateX(180deg);
          .svg path {
            fill: #FFFFFF !important; } }
        &:before {
          opacity: 1; } } }
    .card-body {
      position: relative;
      z-index: 2;
      color: #565656;
      background: transparent;
      border-radius: 0 0 30px 30px;
      padding: 10px 32px 32px;
      transition: all .3s ease;
      margin: 0; } }
  &--klien {
    > .card {
      &:before {
        background: $klien; }
      > .card-header:not(.collapsed) {
        color: #FFF; }
      .card-body {
        color: #FFF; } } } }

.select.select-inline {
  display: inline-block;
  box-shadow: none !important;
  width: auto !important;
  .btn.dropdown-toggle {
    height: auto !important;
    padding: 0;
    padding-left: 10px;
    border: 0 !important;
    background: transparent !important;
    color: $text;
    font-size: 18px !important;
    line-height: 1.3em !important; } }

// Modal
.modal {
  &-dialog {
    min-height: calc(100% - (1.75rem * 2));
    display: flex;
    align-items: center; }
  &-content {
    border-radius: 8px; }
  .close {
    position: absolute;
    top: 24px;
    right: 14px;
    z-index: 9;
    opacity: 1; } }


// Modal Settings
.modal-settings {
  max-width: 920px;
  .close {
    top: 12px; }
  .modal {
    &-content {
      height: calc(100vh - (1.75rem * 2)); }
    &-body {
      display: flex;
      flex-wrap: wrap;
      padding: 0;
      border-radius: 8px;
      overflow: hidden; }
    &-asside {
      flex: 0 0 224px;
      max-width: 224px;
      height: 100%;
      border-right: 1px solid $border;
      background: #fff;
      padding: 20px 12px;
      .nav.nav-tabs {
        .nav-link {
          border: 0;
          &.active {
            border: 0 !important; } } } }
    &-container {
      flex: 0 0 calc(100% - 224px);
      max-width: calc(100% - 224px);
      height: 100%;
      background: #F6F6F6;
      position: relative;
      h4 {
        font-size: 14px;
        line-height: 1.3em;
        font-weight: 600;
        border-bottom: 1px solid $border;
        padding: 16px;
        padding-left: 48px;
        margin-bottom: 0;
        background: #F6F6F6; } }
    &-title {
      margin-bottom: 42px;
      text-align: center;
      h3 {
        font-size: 18px;
        line-height: 1.3em;
        font-weight: 600; } } }
  .settings-tab {
    .nav-tabs {
      border-bottom: 0; }
    .nav {
      &-item {
        display: block;
        width: 100%;
        &:not(:last-child) {
          margin-bottom: 4px; } }
      &-link {
        display: block;
        border: none;
        padding: 12px;
        border-radius: 8px;
        font-size: 14px;
        line-height: 1.3em;
        color: $text;
        &:hover {
          color: $primary; }
        &.active {
          border: none;
          background: #F4D9D3;
          color: $primary;
          font-weight: 500; } } } }
  .tab-content {
    height: calc(100% - 51px);
    padding: 24px 48px;
    overflow-y: auto;
    .tab {
      &-pane {} } }

  .contact-list > ul li:not(:last-child) {
    margin-bottom: 15px; }

  .notfication-settings {
    .settings-item {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      &:not(:last-child) {
        margin-bottom: 40px; } }
    .left {
      b {
        font-size: 22px;
        line-height: 1.3em;
        font-weight: 600;
        color: $primary;
        display: block;
        margin-bottom: 14px; }
      p {
        font-size: 14px;
        line-height: 1.3em;
        margin-bottom: 20px; } }
    .checks {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      min-width: 112px;
      & + .checks {
        margin-top: 8px; }
      .custom-control {
        display: inline-block;
        padding-left: 0;
        padding-bottom: 32px;
        & + .custom-control {
 }          // margin-left: 45px
        &-label {
          font-size: 14px;
          line-height: 1.3em;
          font-weight: 400;
          color: $text;
          text-align: center;
          display: block;
          min-width: 38px;
          &:before,
          &:after {
            left: calc(50% - 10px);
            top: calc(100% + 20px); }
          &:before {
            border: 2px solid #A8A8A8; } } } }

    .form-action {
      text-align: center;
      margin-top: 80px; } }

  .edit-profile {
    .eprofile {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 48px;
      &__pp {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        img {
          width: 100%;
          height: 100%; } }
      &__name {
        margin-left: 14px;
        b {
          font-size: 18px;
          line-height: 1.3em;
          font-weight: 600; }
        .loginas {
          font-size: 14px;
          line-height: 1.3em;
          color: #A8A8A8;
          span {
            color: #000000; } } } }

    .form-action {
