.client-content {
  margin-top: 40px;
  width: calc(100% - 205px);
  max-width: 1088px;
  margin: 50px auto;
  margin-bottom: 0;
  margin-left: 250px; }

.page-title {
  margin-bottom: 16px;
  h2 {
    font-size: 36px;
    line-height: 1.2em;
    font-weight: 600;
    margin-bottom: 0; } }

section {
  margin-bottom: 54px;
  &:last-child {
    margin-bottom: 0; } }

.section_title {
  position: relative;
  margin-bottom: 24px;
  h3 {
    font-size: 18px;
    line-height: 32px;
    font-weight: 600; }
  .show-more {
    position: absolute;
    top: 0;
    right: 0;
    a {
      color: $text;
      &:after {
        content: ">";
        display: inline-block;
        vertical-align: middle;
        margin-left: 9px; } } } }

.client {
  &-home {
    .wrapper_banner {
      margin-bottom: 54px; }
    .welcome-banner {
      padding: 40px 42px;
      background: #fff;
      border: 1px solid #EDEDED;
      box-shadow: 4px 4px 8px #6C6C6C0F;
      border-radius: 8px;
      position: relative;
      .text {
        max-width: 510px; }
      h2 {
        font-size: 26px;
        line-height: 42px;
        font-weight: 700;
        color: $text;
        margin-bottom: 16px;
        img {
          max-height: 38px;
          display: inline-block;
          vertical-align: middle; } }
      p {
        color: $text-light;
        font-size: 14px;
        line-height: 1.5em; }
      .img {
        width: 440px;
        height: 325px;
        object-fit: cover;
        position: absolute;
        top: -35px;
        right: 12px; }
      .btn-primary {
        &:before {
          content: "";
          width: 12px;
          height: 12px;
          display: inline-block;
          background-image: url(../images/ic-plus-btn.png);
          background-size: contain;
          background-position: center;
          background-repeat: no-repeat;
          margin-left: -10px;
          margin-right: 10px; } } } } }

.progress-bar {
  background-color: transparent;
  display: block;
  margin-bottom: 0;
  .pb {
    text-align: left;
    &-block {
      display: inline-block;
      width: 100%;
      height: 12px;
      background: #F4D9D3;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
      span {
        position: absolute;
        height: 12px;
        top: 0;
        left: 0;
        background: $primary;
        border-radius: 10px; } }
    .prcntg {
      display: inline-block;
      vertical-align: middle;
      font-size: 16px;
      line-height: 22px;
      font-weight: bold;
      margin-left: 8px;
      color: #4C9F70; } }
  p {
    font-size: 18px;
    line-height: 32px;
    color: $primary;
    text-align: left;
    font-weight: 600;
    margin-bottom: 0; }
  &.rest {
    .pb {
      &-block {
        span {
          background: #ffbb00; } } }
    .prcntg {
      color: #ffbb00; } } }

.card-white {
  padding: 30px 24px;
  background: #FFF;
  border: 1px solid #EDEDED;
  border-radius: 8px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  .card-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: red;
    opacity: .3; }

  .icon {
    width: 24px;
    height: 24px;
    margin-bottom: 16px;
    img {
      display: block;
      width: 100%;
      height: 100%; } }
  .text {
    margin-bottom: 60px;
    h4 {
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 600;
      margin-bottom: 4px; }
    p {
      font-size: 12px;
      line-height: 20px;
      color: #767676;
      margin-bottom: 0; } }
  .tag {
    position: absolute;
    top: 26px;
    right: 26px;
    span {
      display: inline-block;
      vertical-align: middle;
      padding: 4px 11px;
      border-radius: 5px;
      color: #FFF;
      font-size: 11px;
      line-height: 1.5em; } }
  .btn-white {
    padding: 8px 28px;
    font-size: 14px;
    line-height: 1.5em;
    transition: none; }

  &.yellow {
    &:hover {
      background-color: #FFF7D3;
      border-color: #FFDF4C;
      box-shadow: 4px 4px 8px #FFDF4C33;
      .btn-white {
        background-color: #FFDF4C;
        border-color: #FFDF4C;
        color: #fff; } } }

  &.red {
    &:hover {
      background-color: #F5D9D4;
      border-color: #F73824;
      box-shadow: 4px 4px 8px #F7382433;
      .btn-white {
        background-color: #F73824;
        border-color: #F73824;
        color: #fff; } } }

  &.grey {
    &:hover {
      background-color: #EBEBEB;
      border-color: #9B9B9B;
      box-shadow: 4px 4px 8px #00000033;
      .btn-white {
        background-color: #767676;
        border-color: #767676;
        color: #fff; } } }
  &.green {
    &:hover {
      background-color: #ccffd0;
      border-color: #1cea6f;
      box-shadow: 4px 4px 8px #00000033;
      .btn-white {
        background-color: #1cea6f;
        border-color: #1cea6f;
        color: #fff; } } }

  &.guide {
    padding: 24px 20px;
    .text {
      margin-bottom: 0; }
    h4 {
      font-size: 14px;
      line-height: 1.5em;
      color: $primary; }
    .btn-link {
      font-size: 12px;
      line-height: 18px;
      color: $text;
      padding: 0; } } }

