.proyek__wrap-detail {
  .ic-check {
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    margin-top: -5px;
    margin-left: 8px;
    img {
      width: 100%;
      height: 100%; } }

  .pt-inner {
    display: flex;
    align-items: center; }

  .pryk-type {
    border: 0;
    width: max-content;
    padding: 5px;
    border: 1px solid $border;
    border-radius: 8px;
    .nav {
      &-item {
        margin-right: 8px; }
      &-link {
        padding: 8px 12px;
        font-size: 12px;
        line-height: 1.3em;
        border: 0;
        border-radius: 5px;
        &.active {
          border: 0;
          background: $primary;
          color: #fff; } } } }

  .icn-info {
    width: 24px;
    height: 24px;
    text-align: center;
    border: 1px solid #767676;
    border-radius: 50%;
    position: relative;
    margin-left: 20px;
    cursor: pointer;
    &:hover {
      .box-info {
        opacity: 1;
        visibility: visible; } }
    .box-info {
      position: absolute;
      left: calc(100% + 16px);
      top: 0;
      transition: all .4s ease-in-out;
      opacity: 0;
      visibility: hidden;
      min-width: 200px;
      padding: 10px;
      border: 1px solid #767676;
      border-radius: 8px;
      background: #FFF;
      font-size: 12px;
      line-height: 20px;
      color: #000;
      box-shadow: 4px 4px 8px #6C6C6C0F;
      text-align: left;
      z-index: 2; } }

  .timeline {
    width: 100%;
    max-width: 440px;
    float: right;
    position: relative;
    max-height: 380px;
    overflow: hidden;
    transition: all .4s linear;
    &.showall {
      max-height: max-content; }
    &:before {
      content: "";
      width: 1px;
      height: calc(100% - 100px);
      position: absolute;
      top: 40px;
      left: 54px;
      background: $text; }
    &:after {
      content: "";
      width: 100%;
      height: 100px;
      background: transparent linear-gradient(0deg, #FFFFFF 0%, #FFFFFF00 100%) 0% 0% no-repeat padding-box;
      position: absolute;
      left: 0;
      bottom: 0; }
    &__item {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0; }
      &.inactive {
        .timeline__box {
          border-color: #A8A8A8;
          background: #F6F6F6;
          color: #A8A8A8;
          b {
            color: #A8A8A8; } } } }
    &__box {
      width: 360px;
      min-height: 100px;
      padding: 15px 25px;
      border-radius: 8px;
      border-left: 7px solid $primary;
      background: #F4D9D3;
      position: relative;
      b {
        font-size: 16px;
        line-height: 1.3em;
        color: $primary;
        display: block;
        margin-bottom: 8px; }

      p {
        font-size: 14px;
        line-height: 1.3em; }

      .btn-wrap {
        span {
          display: inline-block;
          vertical-align: middle;
          margin-left: 24px;
          font-size: 14px;
          line-height: 1.3em;
          color: $primary; } }

      &:before {
        content: "";
        border-radius: 50px;
        background: $primary;
        background-image: url('../images/ic-primary-check.png');
        position: absolute;
        left: -45px;
        top: calc(50% - 12px);
        @include bgcontain;
        width: 24px;
        height: 24px; } }
    .more {
      cursor: pointer;
      font-size: 14px;
      line-height: 1.3em;
      color: $text;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0 auto;
      text-decoration: none;
      z-index: 3;
      span,
      .ic {
        display: inline-block;
        vertical-align: middle; }
      .ic {
        width: 12px;
        height: 12px;
        transform: rotate(90deg); } } } }

