/* ------------------------------------------------------------------------------------------------ */
/* Custom Plugins */
/* ------------------------------------------------------------------------------------------------ */

.owl-carousel {
  position: relative;
  * {
    outline: none !important; }
  .owl-nav {
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 50%;
    span {
      display: block;
      padding: 6px;
      opacity: 1;
      border-radius: 21px;
      border: 1px solid transparent;
      transition: all .3s ease;
      img {
        display: block;
        width: 30px;
        height: 30px; }
      &:hover {
        border: 1px solid #f1f1f1;
        background: #FFF;
        box-shadow: 2px 2px 8px #00000020; } }
    button.disabled {
      opacity: 0.5;
      pointer-events: none; }
    button.owl-next {
      position: absolute;
      top: -21px;
      right: -54px; }
    button.owl-prev {
      position: absolute;
      top: -21px;
      left: -54px; } }
  .owl-dots {
    display: block;
    padding-top: 16px;
    font-size: 0;
    line-height: 0;
    text-align: center; }
  button.owl-dot {
    display: inline-block;
    span {
      display: block;
      width: 30px;
      height: 6px;
      background: #E6E6E6;
      border-radius: 3px;
      margin: 0 3px; }
    &.active span {
      background: #000; } } }

.select.bootstrap-select {
  width: 100% !important;
  .dropdown-menu {
    min-width: 100% !important; }
  .dropdown-menu.inner {
    padding: 12px; }
  .dropdown-item {
    display: block;
    padding: 10px;
    color: #767676;
    border-radius: 8px;
    text-decoration: none;
    margin-block-end: 10px;
    &:active,
    &.selected {
      background-color: #F4D9D3; } } }

.datepicker>div {
    display: block; }
.datepicker {
  table {
    width: 100%;
    border-radius: 6px;
    margin-bottom: 16px;
    th,
    td {
      padding: 8px 16px; }
    th {
      font-weight: 400;
      &:first-child {
        color: #F23C13; }
      &:last-child {}
      &.prev,
      &.next {
        font-size: 0;
        background-image: url('../images/ic-caret-right.svg');
        @include bgcontain;
        background-size: 12px;
        &:hover {
          background-image: url('../images/ic-caret-right.svg');
          @include bgcontain;
          background-size: 12px; } }
      &.prev {
        transform: rotate(-180deg); } }
    tbody {
      tr:first-child {
        td {
 } }          // border-top: 1px solid $border
      td {
        &:first-child {
          color: #F23C13; } } }
    th {
      background: #F8F9FA; }
    td {} } }
